
.comment-div {
    padding-top: 20px;
    border-top: 1px solid #ccc;
    margin-top: 20px;
    padding-left: 20px;
}

.comment-div {
    padding-left: 20px;
}

.pl-520am {
    font-family: 'microsoft yahei', sans-serif;
    font-size: 14px;
    color: #333;
}

.pl-520am a {
    color: rgb(68, 112, 142);
    text-decoration: none;
}

.pl-clr {
    clear: both;
}

.pl-fr {
    float: right;
}

.pl-header {
    font-size: 18px;
}

.pl-header em {
    font-weight: normal;
    font-family: Georgia, sans-serif;
}

.pl-header em {
    color: #ee542a;
    font-weight: bold
}

.pl-userinfo {
    float: right;
}

.pl-area {
    padding: 0 0 0 60px;
    margin-top: 15px;
}

/*.pl-area *, .pl-area {*/
/*    background: floralwhite;*/
/*}*/

.pl-userpic {
    width: 42px;
}

.pl-area-userpic {
    margin-left: -60px;
    float: left
}

.pl-area-userpic img {
    width: 40px;
    height: 42px;

}

.pl-area-post {
    float: right;
    width: 100%;
}

.pl-post {
    border: 2px solid #ccd4d9;
    border-radius: 3px
}

.pl-textarea {
    width: 100%;
    padding: 0;
    height: 75px;
    position: relative;
    overflow: hidden;
}

.pl-post-word {
    border: none;
    outline: none;
    width: 99%;
    margin: 5px auto;
    display: block;
    height: 60px;
    line-height: 22px;
    font-size: 14px;
    background: none scroll;
    resize: none;
}

.pl-icon {
    background-image: url(https://ae01.alicdn.com/kf/Ha60866820e9040c0a2c0e44d7684f309T.png);
    background-repeat: no-repeat;
    display: inline-block;
}

.pl-tools {
    border-top: 1px solid #ddd;
    height: 40px
}

.pl-tools ul, .pl-tools ul li {
    list-style: none;
    padding: 0;
    margin: 0;
}

.pl-tools ul li {
    display: inline-block;
    clear: both;
    border-right: 1px solid #ddd;
    padding: 5px 10px 0 5px;
    cursor: pointer;
    height: 35px;
}

.icon-face {
    width: 25px;
    height: 25px;
    background-position: -20px 6px
}

.icon-face:hover {
    background-position: -20px -19px
}

.icon-img {
    width: 21px;
    height: 25px;
    background-position: -49px 7px;
}

.icon-img:hover {
    background-position: -49px -18px
}

.pl-tools-lastchild {
    border: 0;
    float: right;
    position: relative;
    height: 38px !important;
    min-width: 80px
}

.pl-tools-lastchild button {
    border: 0;
    background: #2787B6;
    height: 100%;
    width: 100%;
    position: absolute;
    outline: none;
    color: #fff;
    bottom: -2px;
    top: -1px;
    right: -2px;
    border-radius: 0 0 3px 0;
    font-size: 18px;
    text-shadow: 1px 1px 3px #000;
    font-weight: normal;
    cursor: pointer;
}

.pl-tools-lastchild button:hover, .pl-tools-lastchild button[disabled] {
    opacity: 0.8
}

.pl-showinfo {
    padding: 8px;
    text-align: center;
    background-color: rgb(254, 242, 225);
    color: rgb(238, 84, 42);
    margin-top: 10px;
    display: none;
}

.pl-show-list {
    position: relative;
}

.pl-show-list-loading {
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, .1) no-repeat center;
    position: absolute;
    height: 100%;
    width: 100%;
}

.pl-show-hot-list {
    display: none
}

.pl-title {
    margin-top: 20px;
    background-color: transparent;
    border-bottom: 2px solid rgb(136, 171, 195);
    padding-bottom: 10px;
    font-size: 16px
}

.buttonGray {
    outline: none;
    background: -o-linear-gradient(top, #f3f3f3, #e5e5e5);
    border: 1px solid #b6b6b6;
    border-radius: 3px 3px 3px 3px;
    color: #000;
    font-size: 16px;
    height: 30px;
    line-height: 25px;
    margin: 20px 0;
    text-align: center;
    text-shadow: 1px 1px 1px #FFFFFF;
    width: 100%;
    display: none;
}

.ecomment {
    margin: 0;
    padding: 0;
    word-wrap: break-word
}

.ecomment {
    margin-bottom: 12px;
    overflow-x: hidden;
    overflow-y: hidden;
    padding: 3px;
    border: solid 1px #dedede
}

.ecommentauthor {
    float: left;
    color: #2787b6;
    display: block;
    width: 100%
}

.ecommentauthor i {
    float: right;
    font-style: normal
}

.ecommenttext {
    clear: left;
    margin: 0;
    padding: 0
}

.pl-show-box {
    margin: 15px 0;
    clear: both;
    border-bottom:  1px solid rgb(136, 171, 195);
    padding-bottom: 10px
}

.pl-show-title {
    margin-bottom: 10px
}

.pl-show-time {
    color: #999
}

.pl-show-saytext {
    line-height: 22px;
    font-size: 14px;
    word-wrap: break-word;
}

.pl-show-tools {
    text-align: right;
    padding: 5px 0;
    font-size: 12px;
    line-height: 12px
}

.pl-show-tools i {
    position: relative;
    top: 2px;
    margin: 0 2px 0 5px
}

.pl-show-tools a {
    position: relative !important;
}

.icon-good {
    width: 18px;
    height: 20px;
    background-position: -97px 6px;
}

.icon-bad {
    width: 18px;
    height: 20px;
    background-position: -97px -18px;
}

.icon-good:hover {
    background-position: -122px 6px
}

.icon-bad:hover {
    background-position: -122px -18px
}

.pl-reply {
    margin-left: 15px;
}

.pl-post-reply {
    margin-top: 5px
}

.ShowPlKey {
    position: relative;
    top: -4px;
    line-height: normal;
}

.ShowPlKey input {
    outline: none
}

.ShowPlKey img {
    position: relative;
    top: -1px;
}

.pl-post {
    position: relative;
}

.pl-face-box-before {
    position: absolute;
    padding: 4px 10px 10px 5px;
    background-color: #fff;
    top: -40px;
}

.pl-face-box-before .icon-face {
    background-position: -20px -19px
}

.pl-face-box, .pl-img-box {
    position: absolute;
    max-width: 246px;
    height: auto;
    background: #fff;
    border: 2px solid #ccd4d9;
    left: -2px;
    border-top: 1px solid #ccd4d9;
    display: none;
    z-index: 3
}

.pl-face-box li {
    display: inline-block;
    padding: 10px;
    border-right: 1px solid #eee;
    border-bottom: 1px solid #eee
}

.pl-img-box {
    left: 40px;
    z-index: 2;
}

.pl-img-box-before {
    position: absolute;
    padding: 5px 10px 10px 8px;
    background-color: #fff;
    top: -40px;
}

.pl-img-box-before .icon-img {
    background-position: -49px -19px
}

.pl-img-file {
    padding: 10px;
}

.pl-img-file input {
    outline: none;
    border: 1px solid #ccc;
    padding: 5px;
    width: 150px
}

.pl-img-file button {
    border-radius: 0;
    border: 1px solid #ccc;
    background: none;
    padding: 5px 15px;
    outline: none;
    cursor: pointer;
}

#pl-pagination {
    text-align: center;
    margin-top: 30px
}

.pl-null {
    padding: 10px 0;
}

.pl-loading {
    display: inline-block;
    position: relative;
    height: 15px;
    width: 15px;
    background: no-repeat center;
    background-size: 100%;
    top: 2px;
    margin-right: 5px
}


.review-username{
    color:#2787b6;
}